@import url(https://fonts.googleapis.com/css?family=Lato|Work+Sans:600);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.App {
  text-align: center;
  background: url(/toronto-events/static/media/torontoBlue.bf60dfba.svg);
  background-size: contain;
}

h1 {
  font-size: 6rem;
  font-family: 'Work Sans', sans-serif;
   margin: 0;
  padding: 0;
  color: #F2F2F2;
  text-shadow: 1px 1px #165788,
  2px 2px #165788;
  /* color: #F2F2F2; */
}

h2, h3 {
  margin: 0;
  padding: 0;
  line-height: 1.4;
  font-family: 'Work Sans', sans-serif;
  color: #165788;
}

h2 {
    font-size: 2rem;
}

p, label, input {
  font-family: 'Lato', sans-serif;
}

input, label, select {
  text-transform: capitalize;
}

button {
    border: 2px solid #165788;
    border-radius: 5px;
    padding: 5px 20px;
}

.wrapper {
  width: 80%;
  margin: 0 auto;
}

.savedEventItem {
  background: #F2F2F2;
  box-shadow: 1px 1px 10px grey;
  padding: 10px;
  margin: 20px 0;
  border-radius: 5px;
  border: 10px solid #165788;
  width: 400px;
  height: 240px;
}

.savedItemText {
  height: 160px;
}

.savedEventsFlex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.fa {
  font-size: 2rem;
  color: #165788;
  margin-bottom: 10px;
}


header {
    min-height: calc(100vh - 110px);
    background: cornflowerblue;
    background: url(/toronto-events/static/media/toronto.64e2bcdc.jpg);
    background-position: 0% 50%;
    background-repeat: no-repeat;
    background-size: 140%;
    padding-top: 50px;
}

.titleContainer {
    height: 100px;
    margin: 0 auto;
    margin-bottom: 100px;
}

.titleContainer h2 {
    color:#165788;
}

header form {
    width: 400px;
    height: 250px;
    padding: 20px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 5px;
    box-shadow: 1px 1px 10px grey;
}

form label {
    display: block;
    margin-top: 20px;
}

select {
    width: 300px;
}

#submitForm {
    margin-top: 20px;
    border: 2px solid #165788;
    border-radius: 5px;
    padding: 5px 20px;
}

/* MEDIA QUERIES */

@media (max-width: 800px){
    h1 {
        font-size: 4rem;
    }
    header {
        background-size: cover;
    }
}

@media (max-width: 500px){
    h1 {
        font-size: 3.5rem;
    }
    h2 {
        font-size: 1.5rem;
    }
    header form {
        width: 340px;
    }
}

@media (max-width: 420px){
    header form {
        width: 320px;
        height: 300px;
    }
    select {
        width: 280px;
    }
}
.resultsTitle {
    margin-top: 20px;
    margin-bottom: 8px;
}

.responseFlex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
}

.responseItem {
    width: 400px;
    height: 440px;
    border: 10px solid #165788;
    box-shadow: 1px 1px 10px grey;
    border-radius: 10px;
    display: block;
    margin-bottom: 20px;
    background: #F2F2F2;
}

.imgContainer {
    width: 100%;
    height: 200px;
    overflow: hidden;
}

.imgContainer img {
    display: block;
    width: 100%;
}

.descriptionContainer {
    height: 160px;
    overflow: hidden;
    padding: 10px;
    margin-bottom: 10px;
    /* border: 1px solid black; */
}
footer {
    height: 90px;
    padding-top: 30px;
    background: #165788;
    /* border-top: 5px solid #165788; */
}

footer p {
    font-size: .9rem;
    /* color: #F2F2F2; */
}

footer h3 {
    color: #F2F2F2;
}

@media (max-width: 700px){
    footer {
        padding-top: 25px;
    }
    footer h3 {
        line-height: 0.8;
    }
    span {
        display: block;
    }
}
