header {
    min-height: calc(100vh - 110px);
    background: cornflowerblue;
    background: url(../../assets/toronto.jpg);
    background-position: 0% 50%;
    background-repeat: no-repeat;
    background-size: 140%;
    padding-top: 50px;
}

.titleContainer {
    height: 100px;
    margin: 0 auto;
    margin-bottom: 100px;
}

.titleContainer h2 {
    color:#165788;
}

header form {
    width: 400px;
    height: 250px;
    padding: 20px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 5px;
    box-shadow: 1px 1px 10px grey;
}

form label {
    display: block;
    margin-top: 20px;
}

select {
    width: 300px;
}

#submitForm {
    margin-top: 20px;
    border: 2px solid #165788;
    border-radius: 5px;
    padding: 5px 20px;
}

/* MEDIA QUERIES */

@media (max-width: 800px){
    h1 {
        font-size: 4rem;
    }
    header {
        background-size: cover;
    }
}

@media (max-width: 500px){
    h1 {
        font-size: 3.5rem;
    }
    h2 {
        font-size: 1.5rem;
    }
    header form {
        width: 340px;
    }
}

@media (max-width: 420px){
    header form {
        width: 320px;
        height: 300px;
    }
    select {
        width: 280px;
    }
}